.popup {

	width: 100vw;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	background: rgba(#fff, 0.05);
	z-index: 10;
	transition: 0.6s;
	opacity: 0;
	visibility: hidden;
	pointer-events: none;


	&__content {
		@include adaptiv-value('padding', 74, 20);
		max-width: 762px;
		position: absolute;
		top: -500%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		display: flex;
		flex-direction: column;
		background: #fff;
		z-index: 10;
		border: 1px solid rgba(#6D6D6D, 0.7);
		transition: 0.6s;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		box-shadow: 0px 0px 47px rgba(0, 0, 0, 0.41);
	}

	&__close {
		display: none;
	}

	&__title {
		@include adaptiv-font(36, 22);
		margin-bottom: 20px;
		font-weight: 400;
		text-align: center;
	}

	&__inform {
		font-weight: 300;
        font-size: 14px;
        color: rgba(#000, 0.7);
        text-align: center;
        margin-bottom: 30px;
	}

	&__form {
		display: flex;
		flex-direction: column;
	}

	&__input,
	&__button {
		height: 71px;
		width: 100%;
		max-width: 614px;
		display: flex;
		align-items: center;
		border: none;
		@include adaptiv-value('padding-left', 30, 20);
	}

	&__input {
		border: 1px solid rgba( #000, 0.5);
	}

	&__button {
		justify-content: center;
		font-weight: 600;
        font-size: 24px;
        color: #fff;
        background: #C198B2;
        cursor: pointer;
	}

	&__input:focus,
	&__button:focus {
		outline: none;
	}

	&__label {
		display: block;
		margin: 20px 0 30px 0;
		display: flex;
		align-items: center;
		cursor: pointer;
	}

	&__checkbox {
		display: none;
	}

	&__fake {
		min-width: 19px;
		min-height: 19px;
		display: block;
		border: 1px solid rgba(#000, 0.7);
		position: relative;
		margin-right: 13px;
	}

	&__fake:before {
		content: url(../img/ok.png);
		width: 20px;
		height: 20px;
		position: absolute;
		top: -19%;
		left: 18%;
		opacity: 0;
		transition: 0.5s;
	}

	&__checkbox:checked + &__fake:before {
		opacity: 1;
	}

	&__span {
		font-weight: 300;
        font-size: 14px;
        color: rgba(#000, 0.7);
	}
}

.popup.visible {
	opacity: 1;
	visibility: visible;
	pointer-events: all;
}

.popup__content.active {
	opacity: 1;
	visibility: visible;
	pointer-events: all;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.header {
	background: url(../img/mainfoto.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;

	&__logo {
		padding: 19px 0;
		background: rgba(242, 242, 242, 0.8);
	}

	&__logo-title {
		@include adaptiv-font(48, 24);
		text-align: center;
		color: #6D6D6D;
	}

	&__main {
		padding: 30px 0;
	}

	&__main-container {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__main-inform-item {
		width: 260px;
		color: #6D6D6D;
		background: rgba(255, 255, 255, 0.7);
		@include adaptiv-font(20, 16);
		@include adaptiv-value("margin-bottom", 20, 10);
		@include adaptiv-value("padding", 26, 18);
