	}

	&__main-inform-second-item {
		width: 280px;
	}

	&__main-inform-third-item {
		width: 300px;
	}

	&__main-consultation {
		width: 380px;
		height: 71px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		@include adaptiv-font(24, 18);
		font-weight: 600;
		background: #C198B2;
		color: #fff;
		cursor: pointer;
	}
} 

.main {
	&__nav-list {
		display: flex;
		justify-content: center;
		margin: 55px 0;
	}

	&__nav-item {
		margin-right: 32px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			width: 6px;
			height: 6px;
			background: #000;
			border-radius: 50%;
			left: -13px;
			top: 50%;
			transform: translateY(-50%);
		}
	}

	&__nav-link {
		@include adaptiv-font(18, 16);
		color: #000;
	}
}

.carriedm {
	&__title {
		@include adaptiv-font(36, 24);
		@include adaptiv-value("margin-bottom", 55, 30);
		text-align: center;
