		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-template-rows: 2fr 1fr;
		gap: 20px;
	}

	&__first-card {
		grid-area: 1 / 1 / 2 / 3;
	}

	&__second-card {
		grid-area: 2 / 1 / 3 / 2;
	}

	&__third-card {
		grid-area: 2 / 2 / 3 / 3;
	}

	&__photo-container-img img {
		width: 100%;
		object-fit: cover;
	}

	&__block {
		margin-left: 80px / 1190px * 100%;
	}

	&__caption {
		display: inline-block;
		@include adaptiv-font(24, 20);
		margin-bottom: 25px;
	}

	&__text {
		max-width: 520px;
		text-align: left;
		@include adaptiv-font(16, 14);
		@include adaptiv-value ("line-height", 21, 18);
		font-weight: 300;
		color: rgba(#000, 0.7);
		@include adaptiv-value ("margin-bottom", 50, 30);
	}

	&__little-text {
		margin-bottom: 10px;
		&:last-child {
			@include adaptiv-value ("margin-bottom", 50, 30);
		}
	}

	&__name {
		color: #000;
		@include adaptiv-value ("line-height", 26, 23);
		display: inline-block;
		margin-left: 15px;
	}

	&__last-block {
		display: flex;
		justify-content: space-between;
	}

	&__info {
		margin-left: 36px;
	}



	&__info-span {
		@include adaptiv-font(18, 16);
		@include adaptiv-value ("line-height", 30, 26);
		display: inline-block;
		position: relative;
	}

	&__info-2, 
	&__info-3 {
		margin-left: 36px;
	}

	&__first-info:before {
		content: url(../img/crownp.png);
		display: inline-block;
		position: absolute;
		width: 26px;
		height: 26px;
		left: -36px;
		top: 5px;
	}

	&__second-info:before {
		content: url(../img/peoplep.png);
		display: inline-block;
		position: absolute;
		width: 26px;
		height: 26px;
		left: -36px;
		top: 5px;
	}

	&__third-info:before {
		content: url(../img/coinsp.png);
		display: inline-block;
		position: absolute;
		width: 26px;
		height: 26px;
		left: -36px;
		top: 5px;
	}

	&__info-text {
		@include adaptiv-font(16, 14);
		@include adaptiv-value ("line-height", 26, 23);
		font-weight: 300;
		color: rgba(#000, 0.7);
		max-width: 256px;
		@include adaptiv-value ("margin-bottom", 30, 20);
	}

	&__button {
		font-weight: 600;
		width: 100%;
		height: 71px;
		max-width: 780px;
		background: #C198B2;
		display: flex;
		justify-content: center;
		align-items: center;
		border: none;
		cursor: pointer;
		color: #fff;
		@include adaptiv-font(24, 16);
		margin: 0 auto;
		@include adaptiv-value("margin-top", 60, 30);
