//MIXIN АДАПТИВНЫЙ ШРИФТ

$maxWidth: 1440;
$maxWidthContainer: 1200;

@mixin adaptiv-font($pcSize, $mobileSize) {
	$addSize: $pcSize - $mobileSize;
	$addMobSize: $addSize + $addSize * 0.7;

	@media (max-width: 767px) {
		font-size: calc(#{$mobileSize + px} + #{$addMobSize} * ( (100vw - 320px) / #{$maxWidth}));
	}

	@media (min-width: 767px) {
		font-size: calc(#{$mobileSize + px} + #{$addSize} * ( 100vw / #{$maxWidth}));
		}
}


@mixin adaptiv-value($property, $startSize, $minSize) {
	$addSize: $startSize - $minSize;

	#{$property}: calc( #{$minSize + px} + #{$addSize} * ( (100vw - 320px) / #{$maxWidth - 320}))
}	