  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 15px;
}

.swiper-container {
  border: none;
}

.next-container {
  width: 96%;
  margin: 0 auto;
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.swiper-button-prev:after,
.swiper-button-next:after {
  content: '';
}

.swiper-button-prev:before {
  content: url(../img/arrow2.png);
  margin-right: 15px;
}

.swiper-button-next:before {
  content: url(../img/arrow1.png);
  margin-left: 15px;
}

.slider-picture {
  width: 85%;
