.popup {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.05);
  z-index: 10;
  transition: 0.6s;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.popup__content {
  padding: calc( 20px + 54 * ( (100vw - 320px) / 1120));
  max-width: 762px;
  position: absolute;
  top: -500%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  z-index: 10;
  border: 1px solid rgba(109, 109, 109, 0.7);
  transition: 0.6s;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  box-shadow: 0px 0px 47px rgba(0, 0, 0, 0.41);
}

.popup__close {
  display: none;
}

.popup__title {
  margin-bottom: 20px;
  font-weight: 400;
  text-align: center;
}

@media (max-width: 767px) {
  .popup__title {
    font-size: calc(22px + 23.8 * ( (100vw - 320px) / 1440));
  }
}

@media (min-width: 767px) {
  .popup__title {
    font-size: calc(22px + 14 * ( 100vw / 1440));
  }
}

.popup__inform {
  font-weight: 300;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  margin-bottom: 30px;
}

.popup__form {
  display: flex;
  flex-direction: column;
}

.popup__input, .popup__button {
  height: 71px;
  width: 100%;
  max-width: 614px;
  display: flex;
  align-items: center;
  border: none;
  padding-left: calc( 20px + 10 * ( (100vw - 320px) / 1120));
}

.popup__input {
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.popup__button {
  justify-content: center;
  font-weight: 600;
  font-size: 24px;
  color: #fff;
  background: #C198B2;
  cursor: pointer;
}

.popup__input:focus, .popup__button:focus {
  outline: none;
}

.popup__label {
  display: block;
  margin: 20px 0 30px 0;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.popup__checkbox {
  display: none;
}

.popup__fake {
  min-width: 19px;
  min-height: 19px;
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.7);
  position: relative;
  margin-right: 13px;
}

.popup__fake:before {
  content: url(../img/ok.png);
  width: 20px;
  height: 20px;
  position: absolute;
  top: -19%;
  left: 18%;
  opacity: 0;
  transition: 0.5s;
}

.popup__checkbox:checked + .popup__fake:before {
  opacity: 1;
}

.popup__span {
  font-weight: 300;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
}

.popup.visible {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.popup__content.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

