  margin-top: calc( 70px + 60 * ( (100vw - 320px) / 1120));
  margin-bottom: 150px;
  position: relative;
}

.last__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 27px;
  text-align: center;
  margin-bottom: calc( 30px + 50 * ( (100vw - 320px) / 1120));
}

.last__form {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.last__form-box {
  display: flex;
  flex-direction: column;
}

.last__form-label {
  font-size: 18px;
  font-weight: 400;
}

.last__input {
  height: 71px;
  display: flex;
  align-items: center;
  padding: 0 15px;
  width: 100%;
  max-width: 280px;
  transition: 0.6;
  margin: 20px 0;
  border: 1px solid rgba(0, 0, 0, 0.7);
  margin: 20px 0;
}

.last__input:focus {
  outline: none;
}

.last__main-input {
  font-size: 24px;
}

.last__input:hover {
  border: 1px solid rgba(0, 0, 0, 0.7);
}

.last__next-input {
  max-width: 230px;
}

.last__form-info {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
  display: inline-block;
  max-width: 380px;
}

.last__third-input {
  background: #C198B2;
  color: #fff;
  font-size: 24px;
  justify-content: center;
  max-width: 380px;
  transform: translateY(22px);
  border: none;
  cursor: pointer;
}

.last__third-input:focus {
  outline: none;
  border: none;
}

.last__third-input:hover {
  border: none;
}

.last__next-form-info {
  transform: translateY(20px);
  margin-left: 10px;
}

